import React from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import Typography from '@material-ui/core/Typography';
import Divider from '@material-ui/core/Divider';
import mockData from './mock-data';
import { Redirect } from 'react-router'
import Header from './Header';
import Footer from './Footer';

const styles = theme => ({
  root: {
    width: "100%",
    height: "100vh",
    display: "flex",
    flexDirection: "column"
  },
  content: {
    flexGrow: 1,
    overflowY: "auto",
    marginLeft: 10,
    marginRight: 10
  }
});

function Channel(props){
  return (
          <React.Fragment>
            <ListItem alignItems="flex-start" button  onClick = {props.onClick}>
              <ListItemText
                primary= {props.channel.bp}
                secondary={
                  <React.Fragment>
                    <Typography component="span" color="textPrimary">
                      {props.channel.event}
                    </Typography>
                    {props.channel.productName}
                  </React.Fragment>
                }
              />
            </ListItem>
            <Divider/>
          </React.Fragment>
  );
}

function Channels(props) {
    return (
      <List>
      {props.channels.map(function(channel, index){
                    return <Channel channel= {channel} key={ index } onClick = {()=> props.onClick()} />;
                  })}
      </List> 
    );
}

class ChannelsHome extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      redirectTo: "none",
      actionValue: 0,
      channels: mockData.channels
    };
  }

  componentWillMount() {
    document.body.style.margin = 0;
  }
  
  
  handleAction = (event, value) => {
    this.setState({
      redirectTo: "none",
      actionValue: value,
      channels: mockData.channels
    })
  };

  handleChannelClick = event => {
    this.setState({
      redirectTo: "/conversation",
      actionValue: 0,
      channels: mockData.channels
    })
  }

  render() {
    const { classes } = this.props;

    if(this.state.redirectTo !== "none"){
      return <Redirect to={this.state.redirectTo} />
    }

    if(this.state.actionValue === 2){
      //return <Redirect to='/conversation' />
    }

    return (
      <div className={classes.root}>
        <Header/>
        <div className = {classes.content}>
          <Channels channels={this.state.channels} onClick = {() => this.handleChannelClick()}/>
        </div> 
        <Footer onClick = {(event, value)=> this.handleAction(event, value)}/>
      </div> 
    );
  }
}



ChannelsHome.propTypes = {
  classes: PropTypes.object.isRequired
};

export default withStyles(styles)(ChannelsHome);
